.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background-color: #282c34;
  background-image: url(./paginas/Fondo/98.jpg);
  background-repeat: no-repeat;
  background-position: 0px 15px;
  background-size: cover;
}
h1{
  color: #3a71e0;
}
.ul1{
  color: #ffffff;
}
.p2{
  color: #ffffff;
}
li{
  color: #ffffff;
}
/**TABLA//**/
table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ffffff;
}

th {
  background-color: #bb0909;
}

tr:hover {
  background-color: #664cce;
}

/*FORMULARIO*/
label {
  display: block;
  margin-bottom: 10px;
}

input[type="text"],
input[type="date"] {
  width: 250px;
  padding: 5px;
  border-radius: 4px;
}

button {
  padding: 10px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
label{
  padding: 10px 15px;

  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
